import { Helmet } from 'react-helmet-async';
import PageTitleWrapper from 'src/components/PageTitleWrapper';
import { Grid, Container, Typography, Button, Tooltip, Box } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Link as RouterLink } from 'react-router-dom';
import { useNavigate, useParams } from 'react-router-dom';

import Footer from 'src/components/Footer';
import { useGetInventoryByIdQuery } from 'src/services/baseApi';

import Content from './Content';

function InventoryDelete() {

  let { id } = useParams();
  let inventoryId = parseInt(id);
  const navigate = useNavigate();

  // invalid id
  if (id == undefined) {
    navigate(`/inventories/list`);
  }

  // load inventory
  const { data, isLoading, isSuccess  } = useGetInventoryByIdQuery(
    { inventoryId: inventoryId },
    { refetchOnMountOrArgChange :true, refetchOnFocus: true }
  );

  if (isLoading) return (<h1>Carregando</h1>)
  else if (!isSuccess) {
    navigate(`/inventories/list`);
  }

  return (
    <>
      <Helmet>
        <title>Deletar</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container justifyContent="space-between" alignItems="center">
          <Grid item>
            <Typography variant="h3" component="h3" gutterBottom>
              Deletar
            </Typography>
            {/* <Typography variant="subtitle2">
              Confirme se realmente quer deletar este inventário
            </Typography> */}
          </Grid>
          <Grid item>
              <Tooltip arrow title="Voltar para lista de inventários">
                <Button
                  sx={{ mt: { xs: 2, md: 0 } }}
                  variant="contained"
                  startIcon={<ArrowBackIcon fontSize="small" />}
                  component={RouterLink}
                  to="/inventories/list"
                >
                  Voltar
                </Button>
              </Tooltip>
          </Grid>
        </Grid>
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="stretch"
          spacing={3}
        >
          <Grid item xs={12}>
            <Content
              data={JSON.parse(JSON.stringify(data))}
            />
          </Grid>
        </Grid>
      </Container>
      <Footer />
    </>
  );
}

export default InventoryDelete;
