import { toast } from 'react-toastify';
import { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';

import {
  Button,
  Card,
  CardContent,
  Divider,
  Grid,
  Typography,
  Box,
  Stack,
} from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import DeleteIcon from '@mui/icons-material/Delete';

import { Inventario } from 'src/models/types';
import Text from 'src/components/Text';
import { formatDate } from 'src/services/utils';

import {
  useDeleteInventoryByIdMutation,
} from 'src/services/baseApi';


export default function Content(props) {

  let { id } = useParams();
  let inventoryId = parseInt(id);
  const navigate = useNavigate();

  let inventario: Inventario = JSON.parse(JSON.stringify(props.data))

  const [inventoryForm, setInventoryForm] = useState(inventario);
  const [deleteInventoryByIdMutation] = useDeleteInventoryByIdMutation();

  const buttonDelete = async (event) => {
    event.preventDefault();
    toast.dismiss();

    try {
      const response = await toast.promise(
        deleteInventoryByIdMutation({
          inventoryId: inventario.id
        }).unwrap(),
        {
          pending: 'Aguarde...',
          success: 'Inventário deletado!',
          error:
            'Não foi possível realizar esta etapa. Tente novamente.'
        }
      );

      navigate(`/inventories/list`);

    } catch (error) {
      console.error(error);
    }

  };

  return (
    <Grid container spacing={3}>

      {/* ----------------- CLIENTE -----------------  */}
      <Grid item xs={12}>
        <Card>
          <Box
            p={3}
            display="flex"
            alignItems="center"
            justifyContent="space-between"
          >
            <Box>
              <Typography variant="h4" gutterBottom>
                Deseja deletar o inventário de {inventario.decujus.nome}?
              </Typography>
              <Typography variant="subtitle2">
                Esta ação não pode ser desfeita.
              </Typography>
            </Box>
          </Box>
          <Divider />
          <CardContent sx={{ p: 4 }}>
            <Typography variant="subtitle2">
              <Grid container spacing={1}>
                
                <Grid item xs={12} sm={4} md={3} textAlign={{ sm: 'right' }}>
                  <Box pr={3} pt={0} pb={0}>
                    Inventário de:
                  </Box>
                </Grid>
                <Grid item xs={12} sm={8} md={9}>
                  <Text color="black">
                    <b>{inventario.decujus.nome}</b>
                  </Text>
                </Grid>

                <Grid item xs={12} sm={4} md={3} textAlign={{ sm: 'right' }}>
                  <Box pr={3} pt={0} pb={0}>
                    Cliente / Solicitante:
                  </Box>
                </Grid>
                <Grid item xs={12} sm={8} md={9}>
                  <Text color="black">
                    <b>{inventario.cliente.nome}</b>
                  </Text>
                </Grid>

                <Grid item xs={12} sm={4} md={3} textAlign={{ sm: 'right' }}>
                  <Box pr={3} pt={0} pb={0}>
                    Criado em:
                  </Box>
                </Grid>
                <Grid item xs={12} sm={8} md={9}>
                  <Text color="black">
                    <b>{ formatDate(inventario.data_criacao) }</b>
                  </Text>
                </Grid>

                <Grid item xs={12} sm={4} md={3} textAlign={{ sm: 'right' }}>
                  <Box pr={3} pt={0} pb={0}>
                    Atualizado em:
                  </Box>
                </Grid>
                <Grid item xs={12} sm={8} md={9}>
                  <Text color="black">
                    <b>{ formatDate(inventario.data_atualizacao) }</b>
                  </Text>
                </Grid>

              </Grid>
            </Typography>
          </CardContent>


          {/* ----------------- BOTOES -----------------  */}
          
          <Divider />
          <CardContent sx={{ p: 4 }}>
            <Typography variant="subtitle2">
              <Box
                sx={{
                  '& .MuiTextField-root': { m: 1, width: '98%'}
                }}
              >
                <Stack spacing={1} direction="row">
                  <Button
                    variant="contained"
                    startIcon={<DeleteIcon fontSize="small" />}
                    color="error"
                    onClick={buttonDelete}>
                    Deletar
                  </Button>
                  {/* <Button
                    sx={{ mt: { xs: 2, md: 0 } }}
                    variant="outlined"
                    startIcon={<ArrowBackIcon fontSize="small" />}
                    component={RouterLink}
                    to="/inventories/list"
                  >
                    Voltar
                  </Button> */}
                </Stack>
              </Box>
            </Typography>
          </CardContent>
        </Card>
      </Grid>

      

    </Grid>
  );
}